const ary = Array.from({ length: 10000 }, (_, i) => ({
    isApprovingFeeding: false,

    isApprovingNFT: false,
    isApprovingMission: false,

    isBetting: false,

    isFeeding: false,

    isSendingToMission: false,
    isReturningToWallet: false
}));

export const useRefresh = () => {
    const { fast, slow } = useContext(RefreshContext)
    return { fastRefresh: fast, slowRefresh: slow }
}

export default function MintPage() {
    const { fastRefresh } = useRefresh();

    const chainId = useChainId();
    const { address } = useAccount();
    const { writeContractAsync } = useWriteContract();
    const [tokenIds, setTokenIds] = useState([]);

    ///////////////////////////////////////////////
    const { data: tokenBalance, refetch: refetchTokenBalance } = useReadContract({
        address: chainId === TEST_NET ? GG_ADDRESS_TEST : GG_ADDRESS_MAIN,
        abi: GGAbi,
        functionName: "balanceOf",
        args: [address],
        chainId: chainId,
        enabled: !!address && !!chainId,
    });

    const { data: approvedAmount, refetch: refetchApprovedAmount } = useReadContract({
        address: chainId === TEST_NET ? GG_ADDRESS_TEST : GG_ADDRESS_MAIN,
        abi: GGAbi,
        functionName: "allowance",
        args: [address, chainId === TEST_NET ? MISSION_ADDRESS_TEST : MISSION_ADDRESS_MAIN],
        chainId: chainId,
        enabled: !!address && !!chainId,
    });

    ///////////////////////////////////////////////
    const { data: isApprovedForAll, refetch: refetchIsApprovedForAll } = useReadContract({
        address: chainId === TEST_NET ? NEW_GOTCHA_ADDRESS_TEST : NEW_GOTCHA_ADDRESS_MAIN,
        abi: NewGotchaAbi,
        functionName: "isApprovedForAll",
        args: [address, chainId === TEST_NET ? MISSION_ADDRESS_TEST : MISSION_ADDRESS_MAIN],
        chainId: chainId,
        enabled: !!address && !!chainId,
    });

    ///////////////////////////////////////////////
    const { data: missionPrice, refetch: refetchMissionPrice } = useReadContract({
        address: chainId === TEST_NET ? MISSION_ADDRESS_TEST : MISSION_ADDRESS_MAIN,
        abi: MissionAbi,
        functionName: "missionPrice",
        args: [],
        chainId: chainId,
        enabled: !!address && !!chainId,
    });

    const { data: feedingPrice, refetch: refetchFeedingPrice } = useReadContract({
        address: chainId === TEST_NET ? MISSION_ADDRESS_TEST : MISSION_ADDRESS_MAIN,
        abi: MissionAbi,
        functionName: "feedingPrice",
        args: [],
        chainId: chainId,
        enabled: !!address && !!chainId,
    });

    const { data: missionItemInfos, refetch: refetchMissionItemInfos } = useReadContract({
        address: chainId === TEST_NET ? MISSION_ADDRESS_TEST : MISSION_ADDRESS_MAIN,
        abi: MissionAbi,
        functionName: "getItemInfos",
        args: [tokenIds],
        chainId: chainId,
        enabled: !!address && !!chainId,
    });

    const { data: nftsForUserData, refetch: refetchNFTsForUserData } = useReadContract({
        address: chainId === TEST_NET ? MISSION_ADDRESS_TEST : MISSION_ADDRESS_MAIN,
        abi: MissionAbi,
        functionName: "getTokensForUser",
        args: [address],
        chainId: chainId,
        enabled: !!address && !!chainId,
    });

    const [nftsOnWallet, setNftsOnWallet] = useState([]);
    const [metadatas, setMetadatas] = useState([]);
    const [loading, setLoading] = useState(false);
    const [showFeedAnimation, setShowFeedAnimation] = useState(false);
    const [needApproveGG, setNeedApproveGG] = useState(false);
    const [showRewardPopup, setShowRewardPopup] = useState(false);
    const [rewardAmount, setRewardAmount] = useState(0);
    const [isRewardLoading, setIsRewardLoading] = useState(false);
    const [isClaiming, setIsClaiming] = useState(false);


    const [btnStatus, setBtnStatus] = useState(ary);

    useEffect(() => {
        refetchTokenBalance();
        refetchApprovedAmount();

        refetchIsApprovedForAll();

        refetchNFTsForUserData();

        refetchMissionItemInfos();
    }, [fastRefresh])

    useEffect(() => {
        if (nftsForUserData) {
            const sortedData = nftsForUserData.sort((a, b) => Number(a.token_id) - Number(b.token_id))
            setNftsOnWallet(sortedData);
        }
    }, [nftsForUserData])

    useEffect(() => {
        if (nftsOnWallet?.length > 0) {
            // Get metadata from API for each NFT
            const tokenids = nftsOnWallet.map((item) => {
                return Number(item.token_id);
            });
            setTokenIds(tokenids);
            getMetadatas(tokenids);
        } else if (address && chainId) {
            // If we have address and chainId but no data after 5 seconds, stop loading
            const timer = setTimeout(() => {
                if (loading) {
                    setLoading(false);
                    setTokenIds([]);
                    setMetadatas([]);
                }
            }, 5000);

            return () => clearTimeout(timer);
        }
    }, [nftsOnWallet, address, chainId, loading]);

    useEffect(() => {
        if (approvedAmount == null || missionPrice == null) return;

        if (Number(ethers.formatEther(approvedAmount)) < Number(ethers.formatEther(missionPrice))) {
            setNeedApproveGG(true);
        } else {
            setNeedApproveGG(false);
        }
    }, [approvedAmount, missionPrice]);

    // Track the last NFT used in mission
    const [lastMissionNftId, setLastMissionNftId] = useState(null);

    // Effect to check claimable amount when reward popup is shown
    useEffect(() => {
        if (showRewardPopup && lastMissionNftId !== null) {
            console.log("ðŸŽ¯ Setting up reward popup for NFT:", lastMissionNftId);

            // Start with loading state
            setIsRewardLoading(true);
            setRewardAmount(0);

            // Wait 5 seconds then check for claimable amount
            const timer = setTimeout(async () => {
                try {
                    console.log("ðŸ” Timer triggered for NFT:", lastMissionNftId);

                    // Refetch mission item info to get the latest data
                    const refetchResult = await refetchMissionItemInfos();
                    console.log("ðŸ” Refetch result:", refetchResult);

                    // Use the fresh data from the refetch result, not the stale state
                    const freshMissionItemInfos = refetchResult?.data || missionItemInfos;
                    console.log("ðŸ” Fresh mission item infos:", freshMissionItemInfos);
                    console.log("ðŸ” Token IDs:", tokenIds);

                    // Find the specific NFT that was used in the mission
                    if (freshMissionItemInfos && tokenIds.length > 0) {
                        const tokenIndex = tokenIds.indexOf(Number(lastMissionNftId));
                        console.log("ðŸ” Token index found:", tokenIndex);

                        if (tokenIndex >= 0 && freshMissionItemInfos[tokenIndex]) {
                            console.log("ðŸ” Mission item info at index:", freshMissionItemInfos[tokenIndex]);
                            const claimableAmount = Number(ethers.formatEther(freshMissionItemInfos[tokenIndex].claimableAmount));
                            console.log("ðŸ” Claimable amount:", claimableAmount);

                            setRewardAmount(claimableAmount);
                        } else {
                            console.log(`âŒ NFT #${lastMissionNftId} not found in mission item infos`);
                        }
                    } else {
                        console.log("âŒ No fresh mission item infos or token IDs");
                    }
                } catch (error) {
                    console.error("âŒ Error checking claimable amount:", error);
                } finally {
                    // Always turn off loading after 5 seconds
                    console.log("âœ… Turning off loading");
                    setIsRewardLoading(false);
                }
            }, 5000);

            return () => clearTimeout(timer);
        }
    }, [showRewardPopup, lastMissionNftId]);

    const getMetadatas = async (tokenids) => {
        try {
            const response = await axios.get(`${chainId === TEST_NET ? API_URL_TEST : API_URL_MAIN}/api/newgame/getMetadatas`, {
                params: {
                    tokenids: tokenids
                }
            });
            if (response.data.success) {
                setMetadatas(response.data.data);
                setLoading(false);
            }
        }
        catch (err) {
            console.log(err);
            setLoading(false);
        }
    };

    const formatNextFeedTime = (countDown) => {
        if (countDown <= 0) return "Available";

        const hours = Math.floor(countDown / (3600 * 1000));
        const minutes = Math.floor((countDown % (3600 * 1000)) / (60 * 1000));

        if (hours > 0) {
            return `${hours}h ${minutes}m`;
        } else {
            return `${minutes}m`;
        }
    };

    const handleFeed = async (nft, index) => {
        // Show feed animation
        setShowFeedAnimation(true);

        // Hide animation after 4.5 seconds
        setTimeout(() => {
            setShowFeedAnimation(false);
        }, 4500);

        if (nft) {
            const buttonInner = document.querySelector('.ultra-feed-button-inner');
            if (buttonInner) {
                buttonInner.classList.add('ultra-feed-button-click-effect');
                setTimeout(() => {
                    buttonInner.classList.remove('ultra-feed-button-click-effect');
                }, 500);
            }

            if (needApproveGG) {
                let temp = btnStatus;
                temp[index].isApprovingFeeding = true;
                setBtnStatus(temp);

                await writeContractAsync({
                    address: chainId === TEST_NET ? GG_ADDRESS_TEST : GG_ADDRESS_MAIN,
                    abi: GGAbi,
                    functionName: "approve",
                    args: [chainId === TEST_NET ? MISSION_ADDRESS_TEST : MISSION_ADDRESS_MAIN, UINT256_MAX],
                })
                    .then(() => {
                        temp[index].isApprovingFeeding = false;
                        setBtnStatus(temp);
                    })
                    .finally(() => {
                        temp[index].isApprovingFeeding = false;
                        setBtnStatus(temp);
                    })
                    .catch((err) => {
                        temp[index].isApprovingFeeding = false;
                        setBtnStatus(temp);
                        toast.error("Approval failed.");
                    });
                return;
            }

            let isSubmitted = false;

            let temp = btnStatus;
            temp[index].isFeeding = true;
            setBtnStatus(temp);
            await writeContractAsync({
                address: chainId === TEST_NET ? MISSION_ADDRESS_TEST : MISSION_ADDRESS_MAIN,
                abi: MissionAbi,
                functionName: "feed",
                args: [nft.tokenid],
            })
                .then(() => {
                    temp[index].isFeeding = false;
                    setBtnStatus(temp);
                    isSubmitted = true;
                })
                .finally(() => {
                    temp[index].isFeeding = false;
                    setBtnStatus(temp);
                })
                .catch((err) => {
                    console.log("handleFeed error = ", err);
                    temp[index].isFeeding = false;
                    setBtnStatus(temp);
                    return;
                });

            if (isSubmitted) {
                try {
                    const response = await axios.post(`${chainId === TEST_NET ? API_URL_TEST : API_URL_MAIN}/api/newgame/updateLevel`, {
                        nftId: nft.tokenid
                    });
                    if (response.data.success === true) {

                    } else {
                        toast.error(response.data.message);
                    }
                } catch (error) {
                    console.log(error);
                    toast.error("An error occurred while feeding.");
                }
            }
        } else {
            toast.error("Please select a NFT to feed.");
        }
    };

    const handleApproveForAll = async (nft, index) => {
        if (nft) {
            if (!isApprovedForAll) {
                let temp = btnStatus
                temp[index].isApprovingNFT = true;
                setBtnStatus(temp);
                await writeContractAsync({
                    address: chainId === TEST_NET ? NEW_GOTCHA_ADDRESS_TEST : NEW_GOTCHA_ADDRESS_MAIN,
                    abi: NewGotchaAbi,
                    functionName: "setApprovalForAll",
                    args: [chainId === TEST_NET ? MISSION_ADDRESS_TEST : MISSION_ADDRESS_MAIN, true],
                })
                    .then(() => {
                        temp[index].isApprovingNFT = false;
                        setBtnStatus(temp);
                    })
                    .finally(() => {
                        temp[index].isApprovingNFT = false;
                        setBtnStatus(temp);
                    })
                    .catch((err) => {
                        temp[index].isApprovingNFT = false;
                        setBtnStatus(temp);
                        toast.error("Approval failed.");
                    });
                return;
            }
        } else {
            toast.error("Please select a NFT to approve.");
        }
    }

    const handleSendMission = async (nft, index) => {
        if (nft) {
            let temp = btnStatus;
            temp[index].isSendingToMission = true;
            setBtnStatus(temp);

            await writeContractAsync({
                address: chainId === TEST_NET ? MISSION_ADDRESS_TEST : MISSION_ADDRESS_MAIN,
                abi: MissionAbi,
                functionName: "sendToMission",
                args: [nft.tokenid],
            })
                .then(() => {
                    temp[index].isSendingToMission = false;
                    setBtnStatus(temp);
                    // Removed popup trigger from here
                    toast.success("Sent a NFT to Mission Successfully! Please start Mission!");
                })
                .finally(() => {
                    temp[index].isSendingToMission = false;
                    setBtnStatus(temp);
                })
                .catch((err) => {
                    temp[index].isSendingToMission = false;
                    setBtnStatus(temp);
                    console.error("Mission error:", err);
                });
        } else {
            toast.error("Please select a NFT to send to Mission.");
        }
    }

    const handleReturnWallet = async (nft, index) => {
        if (nft) {
            let temp = btnStatus;
            temp[index].isReturningToWallet = true;
            setBtnStatus(temp);
            await writeContractAsync({
                address: chainId === TEST_NET ? MISSION_ADDRESS_TEST : MISSION_ADDRESS_MAIN,
                abi: MissionAbi,
                functionName: "returnToWallet",
                args: [nft.tokenid],
            })
                .then(() => {
                    temp[index].isReturningToWallet = false;
                    setBtnStatus(temp);
                    // Removed popup trigger from here
                    toast.success("Returned a NFT to Your Wallet Successfully!");
                })
                .finally(() => {
                    temp[index].isReturningToWallet = false;
                    setBtnStatus(temp);
                })
                .catch((err) => {
                    temp[index].isReturningToWallet = false;
                    setBtnStatus(temp);
                    console.error("Mission error:", err);
                });
        } else {
            toast.error("Please select a NFT to return to Your Wallet.");
        }
    }

    const handleMission = async (nft, index) => {
        if (nft) {
            if (needApproveGG) {
                let temp = btnStatus;
                temp[index].isApprovingMission = true;
                setBtnStatus(temp);

                await writeContractAsync({
                    address: chainId === TEST_NET ? GG_ADDRESS_TEST : GG_ADDRESS_MAIN,
                    abi: GGAbi,
                    functionName: "approve",
                    args: [chainId === TEST_NET ? MISSION_ADDRESS_TEST : MISSION_ADDRESS_MAIN, UINT256_MAX],
                })
                    .then(() => {
                        temp[index].isApprovingMission = false;
                        setBtnStatus(temp);
                    })
                    .finally(() => {
                        temp[index].isApprovingMission = false;
                        setBtnStatus(temp);
                    })
                    .catch((err) => {
                        temp[index].isApprovingMission = false;
                        setBtnStatus(temp);
                        toast.error("Approval failed.");
                    });
                return;
            }

            let temp = btnStatus;
            temp[index].isBetting = true;
            setBtnStatus(temp);
            await writeContractAsync({
                address: chainId === TEST_NET ? MISSION_ADDRESS_TEST : MISSION_ADDRESS_MAIN,
                abi: MissionAbi,
                functionName: "startMission",
                args: [nft.tokenid],
            })
                .then(() => {
                    temp[index].isBetting = false;
                    setBtnStatus(temp);
                    // Removed popup trigger from here
                    toast.success("Mission started! Wait for rewards to become claimable.");
                })
                .finally(() => {
                    temp[index].isBetting = false;
                    setBtnStatus(temp);
                })
                .catch((err) => {
                    temp[index].isBetting = false;
                    setBtnStatus(temp);
                    console.error("Mission error:", err);
                });
        } else {
            toast.error("Please select a NFT to start Mission.");
        }
    }

    const handleClaim = async (nft, index) => {
        if (nft) {
            // Set up for exciting 5-second reveal
            setLastMissionNftId(nft.tokenid);
            setShowRewardPopup(true);
            setIsRewardLoading(true); // Start the excitement!
            setRewardAmount(0); // Reset for suspense

            // The existing useEffect will handle the 5-second delay and reward reveal
            // After the popup closes or user clicks claim in popup, we'll actually claim

        } else {
            toast.error("Please select a NFT to claim.");
        }
    }


    return (
        <Layout>
            <div className="balance-container">
                <div className="balance-item">
                    GOTCHA Balance: <span className="balance-value">{tokenIds.length}</span>
                </div>
                <div className="balance-item">
                    $GG Balance: <span className="balance-value">{tokenBalance ? parseInt(ethers.formatEther(tokenBalance)) : 0}</span>
                </div>
            </div>

            <div className="weekly-mission-container">
                <div className="mission-banner-wrapper">
                    <div className="mission-banner-image">
                        <img src={bannerImg} alt="Weekly Mission" />
                        <div className="mission-overlay"></div>
                        <div className="mission-badge">WEEKLY MISSION</div>
                    </div>
                    <div className="mission-banner-content">
                        <h2 className="mission-title">MISSION 1: THE FORGOTTEN FLAME</h2>
                        <div className="mission-level">
                            <div className="level-indicator">
                                <span className="level-star">â˜…</span>
                                <span className="level-star">â˜…</span>
                                <span className="level-star">â˜…</span>
                            </div>
                            <div className="elemental-essence">Elemental Essence: Fire Essence</div>
                        </div>
                        <p className="mission-description">
                            A tomb of a forgotten guardian has been discovered within a temple carved deep into an ash-covered mountain.
                            Legends say this is the first place where the Fire Essence was sealed.
                            After the recent volcanic eruption, the inner chamber of the tomb may have been exposed.
                        </p>
                        <p className="mission-objective">
                            <strong>Objective:</strong> Find the Fire Essence hidden within the tomb, buried among the burning stones.
                        </p>
                        <div className="mission-reward">
                            <span className="reward-icon fire-crystal"></span> Reward Pool: $GG
                        </div>
                        <div className="mission-rewards">
                            <div className="reward-item">
                                <div className="reward-icon fire"></div>
                                <span className="reward-label">FIRE CRYSTAL</span>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
            <div className="auto-feed-container">
                <div className="auto-feed-content">
                    {loading ? (
                        <div className="loading-container">
                            <div className="loading-spinner"></div>
                            <p>Loading your NFTs...</p>
                        </div>
                    ) : metadatas.length === 0 ? (
                        <div className="no-nfts-message">
                            <p>You don't have any NFTs in your wallet.</p>
                        </div>
                    ) : (
                        <div className="nft-grid">

                            {missionItemInfos && metadatas.map((nft, index) => {
                                const isAvailableMission = Number(missionItemInfos[index].lastPlayTime) * 1000 + 24 * 3600 * 1000 - Date.now() <= 0;
                                const isAvailableClaim = Number(ethers.formatEther(missionItemInfos[index].claimableAmount)) > 0;
                                const xp = Number(missionItemInfos[index].xp);
                                const level = level_types.find(item => xp >= item.start && xp <= item.end).level;
                                const missionCooldown = Number(missionItemInfos[index].lastPlayTime) * 1000 + 24 * 3600 * 1000 - Date.now();

                                return (
                                    <div
                                        key={nft.tokenid}
                                        className={`nft-mission-card ${index % 3 === 0 ? 'fire-type' : index % 3 === 1 ? 'water-type' : 'earth-type'}`}
                                    >
                                        <div className="nft-info-section">
                                            <div className="nft-image-container">
                                                <img src={nft.image} alt={nft.name} className="nft-image" />
                                                <div className="nft-level-badge">LVL {level}</div>
                                            </div>
                                            <div className="nft-details">
                                                <div className="nft-id">#{nft.tokenid}</div>
                                                <div className="nft-name">{nft.name}</div>
                                                {missionCooldown > 0 && (
                                                    <div className="mission-status">
                                                        <div className="status-icon">â°</div>
                                                        <div className="status-text">
                                                            <span className="status-label">On Mission</span>
                                                            <span className="status-time">{formatNextFeedTime(missionCooldown)}</span>
                                                        </div>
                                                    </div>
                                                )}
                                                {isAvailableClaim && (
                                                    <div className="reward-ready">
                                                        <div className="reward-icon">ðŸŽ</div>
                                                        <span className="reward-text">Treasure Ready!</span>
                                                    </div>
                                                )}
                                            </div>
                                        </div>

                                        <div className="nft-actions-section">
                                            <div className="action-group">
                                                <div className="action-info">
                                                    <span className="action-label">Feed Guardian</span>
                                                    <span className="action-cost">{Number(ethers.formatEther(feedingPrice))} $GG</span>
                                                </div>
                                                <button
                                                    className={`action-button feed-button ${btnStatus[index].isFeeding || btnStatus[index].isApprovingFeeding ? 'loading' : ''}`}
                                                    onClick={() => {
                                                        if (!btnStatus[index].isFeeding) {
                                                            handleFeed(nft, index);
                                                        }
                                                    }}
                                                    disabled={btnStatus[index].isFeeding || btnStatus[index].isApprovingFeeding}
                                                >
                                                    <div className="button-content">

                                                        <span className="button-text">
                                                            {needApproveGG ?
                                                                (btnStatus[index].isApprovingFeeding ? 'Approving...' : 'Approve GG') :
                                                                (btnStatus[index].isFeeding ? 'Feeding...' : 'Feed')
                                                            }
                                                        </span>
                                                    </div>
                                                </button>
                                            </div>

                                            <div className="action-group">
                                                <div className="action-info">
                                                    <span className="action-label">Mission</span>
                                                    <span className="action-cost">{Number(ethers.formatEther(missionPrice))} $GG</span>
                                                </div>
                                                {
                                                    isAvailableMission &&
                                                    (
                                                        !isApprovedForAll ?
                                                            <button
                                                                className={`action-button mission-button ${btnStatus[index].isApprovingNFT ? 'loading' : ''}`}
                                                                onClick={() => {
                                                                    if (!btnStatus[index].isApprovingNFT) {
                                                                        handleApproveForAll(nft, index);
                                                                    }
                                                                }}
                                                                disabled={btnStatus[index].isApprovingNFT}
                                                            >
                                                                <div className="button-content">
                                                                    <span className="button-icon">âš”ï¸</span>
                                                                    <span className="button-text">
                                                                        {
                                                                            btnStatus[index].isApprovingNFT ? 'Approving...' : 'Approve NFT'
                                                                        }
                                                                    </span>
                                                                </div>
                                                            </button>
                                                            :
                                                            (
                                                                nftsOnWallet[index].is_staked ?
                                                                    <button
                                                                        className={`action-button mission-button ${btnStatus[index].isReturningToWallet ? 'loading' : ''}`}
                                                                        onClick={() => {
                                                                            if (!btnStatus[index].isReturningToWallet) {
                                                                                handleReturnWallet(nft, index);
                                                                            }
                                                                        }}
                                                                        disabled={btnStatus[index].isReturningToWallet}
                                                                    >
                                                                        <div className="button-content">
                                                                            <span className="button-icon">â†©</span>
                                                                            <span className="button-text">
                                                                                {
                                                                                    btnStatus[index].isReturningToWallet ? 'Returning...' : 'Return To Wallet'
                                                                                }
                                                                            </span>
                                                                        </div>
                                                                    </button>
                                                                    :
                                                                    <button
                                                                        className={`action-button mission-button ${btnStatus[index].isSendingToMission ? 'loading' : ''}`}
                                                                        onClick={() => {
                                                                            if (!btnStatus[index].isSendingToMission) {
                                                                                handleSendMission(nft, index);
                                                                            }
                                                                        }}
                                                                        disabled={btnStatus[index].isSendingToMission}
                                                                    >
                                                                        <div className="button-content">
                                                                            <span className="button-icon">â†ª</span>
                                                                            <span className="button-text">
                                                                                {
                                                                                    btnStatus[index].isSendingToMission ? 'Sending...' : 'Send To Mission'
                                                                                }
                                                                            </span>
                                                                        </div>
                                                                    </button>
                                                            )
                                                    )
                                                }

                                                {!isAvailableMission ? (
                                                    <div className="mission-cooldown">
                                                        <div className="cooldown-content">
                                                            <span className="cooldown-icon">â±ï¸</span>
                                                            <span className="cooldown-text">On Mission</span>
                                                            <span className="cooldown-time">{formatNextFeedTime(missionCooldown)}</span>
                                                        </div>
                                                    </div>
                                                ) : (
                                                    <button
                                                        className={`action-button mission-button ${btnStatus[index].isBetting || btnStatus[index].isApprovingMission ? 'loading' : ''}}`}
                                                        onClick={() => {
                                                            if (!btnStatus[index].isBetting) {
                                                                handleMission(nft, index);
                                                            }
                                                        }}
                                                        disabled={btnStatus[index].isBetting || btnStatus[index].isApprovingMission || !nftsOnWallet[index].is_staked}
                                                    >
                                                        <div className="button-content">
                                                            <span className="button-icon">âš”ï¸</span>
                                                            <span className="button-text">
                                                                {needApproveGG ?
                                                                    (btnStatus[index].isApprovingMission ? 'Approving...' : 'Approve GG') :
                                                                    (btnStatus[index].isBetting ? 'Deploying...' : 'Start Mission')
                                                                }
                                                            </span>
                                                        </div>
                                                    </button>
                                                )}
                                            </div>

                                            <div className="action-group">
                                                {isAvailableClaim ? (
                                                    <button
                                                        className={`action-button claim-button treasure-ready ${nft.isClaiming ? 'loading' : ''}`}
                                                        onClick={() => {
                                                            if (!nft.isClaiming) {
                                                                handleClaim(nft, index);
                                                            }
                                                        }}
                                                        disabled={nft.isClaiming}
                                                    >
                                                        <div className="button-content">
                                                            <span className="button-icon">ðŸŽ</span>
                                                            <span className="button-text">
                                                                {nft.isClaiming ? 'Opening...' : 'Open Treasure!'}
                                                            </span>
                                                        </div>
                                                        <div className="treasure-glow"></div>
                                                    </button>
                                                ) : (
                                                    <div className="no-reward">
                                                        <span className="no-reward-text">No treasures yet</span>
                                                    </div>
                                                )}
                                            </div>
                                        </div>
                                    </div>
                                );
                            })}
                        </div>
                    )}

                    {/* Feed Animation Overlay */}
                    {showFeedAnimation && (
                        <div className="feed-animation-overlay">
                            <div className="feed-animation-container">
                                <img
                                    src="/assets/autobBattle/BossIcon.png"
                                    alt="Feed"
                                    className="feed-icon"
                                />
                                <img
                                    src="/assets/autobBattle/Fast.png"
                                    alt="Clock"
                                    className="fast-icon"
                                />
                                <div className="feed-text">Feeding...</div>
                            </div>
                        </div>
                    )}
                </div>
            </div>

            {/* Reward Popup */}
            {showRewardPopup && (
                <div className="gamified-reward-overlay" onClick={() => setShowRewardPopup(false)}>
                    {isRewardLoading ? (
                        <div className="gamified-reward-container loading-theme" onClick={(e) => e.stopPropagation()}>
                            <div className="gamified-loading-container">
                                <div className="gamified-loading-effect">
                                    <div className="gamified-loading-spinner"></div>
                                    <div className="gamified-loading-flames"></div>
                                </div>
                                <div className="gamified-loading-text">EXPLORING THE TEMPLE</div>
                                <div className="gamified-loading-progress">
                                    <div className="gamified-loading-progress-bar"></div>
                                </div>
                                <div className="gamified-loading-subtext">
                                    <span className="typing-animation">Your character is navigating through..</span>
                                </div>
                            </div>
                        </div>
                    ) : (
                        <div className={`gamified-reward-container ${rewardAmount === 0 ? 'lost' : rewardAmount === 50000 ? 'legendary' : rewardAmount === 20000 ? 'epic' : rewardAmount === 10000 ? 'rare' : rewardAmount === 5000 ? 'uncommon' : 'uncommon'}`} onClick={(e) => e.stopPropagation()}>
                            <img
                                className="gamified-reward-image"
                                src={rewardAmount === 0 ? mission0Img :
                                    rewardAmount === 50000 ? mission50Img :
                                        rewardAmount === 20000 ? mission20Img :
                                            rewardAmount === 10000 ? mission10Img :
                                                rewardAmount === 5000 ? mission5Img :
                                                    mission5Img}
                                alt="Mission result"
                            />
                            <div className="gamified-reward-content">
                                <div className="gamified-reward-title">
                                    {rewardAmount === 0 ? 'MISSION FAILED!' :
                                        rewardAmount === 50000 ? 'LEGENDARY DISCOVERY!' :
                                            rewardAmount === 20000 ? 'EPIC TREASURE!' :
                                                rewardAmount === 10000 ? 'RARE FIND!' :
                                                    rewardAmount === 5000 ? 'SUCCESSFUL MISSION!' :
                                                        'SUCCESSFUL MISSION!'}
                                </div>
                                <div className="gamified-reward-story">
                                    {rewardAmount === 0 ?
                                        "You didn't notice the trap while opening the tomb, and lava suddenly started flowing into the temple. You barely escaped with your life!" :
                                        rewardAmount === 50000 ?
                                            "Congratulations! You reached the tomb in the temple. It seems someone was here before but stayed too long. You've brought back the Fire Element essence and a treasure chest!" :
                                            rewardAmount === 20000 ?
                                                "Congratulations! You reached the tomb in the temple. It seems someone was here before but fled from the lava eruptions. You've brought back the Fire Element essence and two diamonds!" :
                                                rewardAmount === 10000 ?
                                                    "Congratulations! You reached the tomb in the temple. Someone was here before you but things didn't go well for them. You've brought back the Fire Element essence!" :
                                                    rewardAmount === 5000 ?
                                                        "Congratulations! You've successfully navigated through part of the temple and found a small treasure. The Fire Element essence awaits deeper within!" :
                                                        "Congratulations! You've successfully completed your mission and earned a reward!"}
                                </div>
                                <div className="gamified-reward-amount-container">
                                    <div className="gamified-reward-amount">
                                        {rewardAmount.toFixed(0)}
                                    </div>
                                    <div className="gamified-reward-currency">$GG</div>
                                </div>
                                <div className="gamified-reward-buttons">
                                    {rewardAmount > 0 && (
                                        <button className="gamified-reward-button claim" onClick={async () => {
                                            // Find the NFT that matches lastMissionNftId
                                            const matchingNft = metadatas.find(nft => Number(nft.tokenid) === Number(lastMissionNftId));

                                            if (matchingNft) {
                                                // Execute actual blockchain claim
                                                setIsClaiming(true);
                                                try {
                                                    await writeContractAsync({
                                                        address: chainId === TEST_NET ? MISSION_ADDRESS_TEST : MISSION_ADDRESS_MAIN,
                                                        abi: MissionAbi,
                                                        functionName: "claimReward",
                                                        args: [matchingNft.tokenid],
                                                    }).then(() => {
                                                        setIsClaiming(false);
                                                    }).finally(() => {
                                                        setIsClaiming(false);
                                                    }).catch((err) => {
                                                        setIsClaiming(false);
                                                        console.error("Mission error:", err);
                                                    });
                                                    toast.success(`Successfully claimed ${rewardAmount} $GG!`);
                                                } catch (err) {
                                                    console.error("Claim error:", err);
                                                    toast.error("Failed to claim reward");
                                                }
                                                setShowRewardPopup(false);
                                            }
                                        }}>
                                            <span>{isClaiming ? 'Claiming...' : 'Claim Reward'}</span>
                                        </button>
                                    )}
                                    <button className="gamified-reward-button dismiss" onClick={() => setShowRewardPopup(false)}>
                                        <span>{rewardAmount > 0 ? 'Claim Later' : 'Return to Base'}</span>
                                    </button>
                                </div>
                            </div>
                        </div>
                    )}
                </div>
            )}
        </Layout>
    );
}